<template>
  <div>
    color-picker
    <div v-for="(color,idx) in colors" :key="idx" class="color-box" :style="boxBackgrounds[idx]"></div>
  </div>
</template>

<script>
export default {
  name: "color-picker",
  props: {},
  data() {
    return{
      colors: [
        "#FE2E2E",
        "#FE642E",
        "#FE9A2E",
        "#FACC2E",
        "#F7FE2E",
        "#C8FE2E",
        "#9AFE2E",
        "#2EFE2E",
        "#2EFEC8",
        "#2ECCFA",
        "#2E64FE",
        "#642EFE",
        "#CC2EFA",
        "#FE2EC8",
        "#A4A4A4"
      ]
    }
  },
  computed:{
    boxBackgrounds(){
      return this.colors.map(color=> `background-color:${color}`)
    }
  },
  components: {}
};
</script>

