
<template>
  <section class="toy-details">
    <img
      v-if="isLoading"
      src="https://thumbs.gfycat.com/UnfitImportantBats-max-1mb.gif"
    />
    <div v-else>
      {{ toy }}
      <button @click="removeToy(toy._id)">X</button>
      <button @click="editToy(toy._id)">Ed</button>
    </div>
  </section>
</template>

<script>
import { toyService } from "../services/toy.service.js";
_ = require("lodash");

export default {
  name: "toy-details",
  data() {
    return {
      toy: null
    };
  },
  computed: {
    isLoading() {
      return this.toy ? false : true;
    }
  },
  created() {
    this.loadToy();
  },
  watch: {
    "$route.params.id"() {
      this.loadToy();
    }
  },
  methods: {
    loadToy() {
      const id = this.$route.params.id;
      toyService.getById(id).then(toy => {
        this.toy = _.cloneDeep(toy);
      });
    },
    removeToy(id) {
      this.$store.dispatch({ type: "removeToy", id });
      this.$router.push(`/toys`)
    },
    editToy(id){
      this.$router.push(`/edit/${id}`)
    },
  }
};
</script>