<template>
  <section class="toy-list">
    <button @click="goToEdit">Add</button>
    <div v-for="(toy,idx) in toys" :key="idx" class="toy-preview">
      <toy-preview :toy="toy"/>
      <button @click="removeToy(toy._id)">X</button>
      <button @click="editToy(toy._id)">Ed</button>
    </div>
  </section>
</template>

<script>
import toyPreview from "./toy-preview.cmp.vue";

export default {
  name: 'toy-list',
  props:{
    toys:{
      type:Array,
      required: true
    }
  },
  methods:{
    removeToy(id){
      this.$emit('remove', id)
    },
    editToy(id){
      this.$router.push(`/edit/${id}`)
    },
    goToEdit(){
      this.$router.push(`/edit`)
    },
  },
  components: {
    toyPreview,
  }
}
</script>

