import { toyService } from '../services/toy.service.js';

export const toyStore = {
	strict: true,
	state: {
		toys: [],
		filterBy: {
			name: '',
			type: '',
			inStock: false,
			orderBy: 'name,asc',
		},
		isLoading: false,
	},
	getters: {
		toys(state) {
			return state.toys;
		},
		isLoading(state) {
			return state.isLoading;
		},
	},
	mutations: {
		setIsLoading(state, { isLoading }) {
			state.isLoading = isLoading;
		},
		setToys(state, { toys }) {
			state.toys = toys;
		},
		setFilter(state, { filterBy }) {
			state.filterBy = filterBy;
		},
		removeToy(state, { id }) {
			const toyIdx = state.toys.findIndex(toy => toy._id === id);
			state.toys.splice(toyIdx, 1);
		},
		addToy(state, { savedToy }) {
			state.toys.push(savedToy);
		},
		updateToy(state, { savedToy }) {
			const idx = state.toys.findIndex(toy => toy._id === savedToy._id);
			state.toys.splice(idx, 1, savedToy);
		},
	},
	actions: {
		loadToys({ commit, state }) {
			commit({ type: 'setIsLoading', isLoading: true });
			return toyService
				.query(state.filterBy)
				.then(toys => {
					commit({ type: 'setToys', toys });
					commit({ type: 'setIsLoading', isLoading: false });
					return toys;
				})
				.catch(err => err);
		},
		saveToy({ commit }, { toy }) {
			const type = toy._id ? 'updateToy' : 'addToy';
			return toyService.save(toy).then(savedToy => {
				commit({ type, savedToy });
				return savedToy;
			});
		},
		removeToy({ commit }, { id }) {
			return toyService.remove(id).then(() => {
				commit({ type: 'removeToy', id });
			});
		},
	},
	modules: {},
};
