<template>
  <section>
    Toys Display
    <toy-filter v-if="toys" @filter="setFilter"/>
    <chat-app/>
    <img
      v-if="isLoading"
      src="https://thumbs.gfycat.com/UnfitImportantBats-max-1mb.gif"
    />
    <toy-list v-else @remove="removeToy" @filter="setFilter" :toys="toys" />
  </section>
</template>

<script>
import toyList from "../components/toy-list.cmp.vue";
import toyFilter from "../components/toy-filter.cmp.vue";
import chatApp from "../components/chat-app.cmp.vue";
const _ = require("lodash");

export default {
  name: "toy-app",
  computed: {
    toys() {
      return this.$store.getters.toys;
    },
    isLoading() {
      return this.$store.getters.isLoading;
    }
  },
  created() {
    this.$store.dispatch({ type: "loadToys" });
    this.setFilter = _.debounce(this.setFilter,500)
  },

  methods: {
    removeToy(id) {
      this.$store.dispatch({ type: "removeToy", id });
    },
    setFilter(filter) {
      this.$store.commit({ type: "setFilter", filterBy: _.cloneDeep(filter) });
      this.$store.dispatch({ type: "loadToys"});
    }
  },
  components: {
    toyList,
    toyFilter,
    chatApp
  }
};
</script>

