    return{
      userInput:'',
      chatFlow:[]
    }
  },
  computed:{
    msgsToShow(){
      return this.chatFlow
    }
  },
  created(){
    document.addEventListener('keyup',(ev)=>{
    if (ev.key === 'Escape') console.log('idov');
    })
  },
  methods:{
