const axios = require('axios');

export const toyService = {
	query,
	save,
	remove,
	getById,
    getEmptyToy,
};

function _getUrl(id = '') {
	const BASE_URL = process.env.NODE_ENV !== 'development' ? '/api/toy' : '//localhost:3000/api/toy';
	return `${BASE_URL}/${id}`;
}

function query(filterBy) {
	const orderBy = filterBy.orderBy.split(',');
	const sortPart = `_sort=${orderBy[0]}&_order=${orderBy[1]}`;
	const inStock = filterBy.inStock ? `inStock=${filterBy.inStock}` : '';
	const type = filterBy.type ? `type=${filterBy.type}` : '';
	// return axios.get(`http://localhost:3000/toy?name_like=${filterBy.name}&${inStock}&${type}&${sortPart}`)
	return axios.get(_getUrl()).then(res => res.data);
}

function getEmptyToy() {
	return {
		txt: '',
		isDone: false,
	};
}

function getById(id) {
	return axios.get(_getUrl(id)).then(res => res.data);
}

function remove(id) {
	return axios.delete(_getUrl(id)).then(res => res.data);
}

function save(toy) {
	return toy._id ? _update(toy) : _add(toy);
}

function _update(toy) {
	return axios.put(_getUrl(toy._id), toy).then(res => res.data);
}

function _add(toy) {
	return axios.post(_getUrl(toy._id), toy).then(res => res.data);
}
