
<template>
  <section class="toy-edit">
    <img
      v-if="isLoading"
      src="https://thumbs.gfycat.com/UnfitImportantBats-max-1mb.gif"
    />
    <div v-else>
      {{ toy }}
      <form @submit.prevent="saveToy">
        <label for="name">Toy: </label>
        <input v-model="toy.name" id="name" type="text" />
        <label for="price">Price: </label>
        <input v-model="toy.price" id="price" type="number" />
        <label for="type">Type: </label>
        <select v-model="toy.type" name="" id="type">
          <option>Funny</option>
          <option>Educational</option>
          <option>Adult</option>
        </select>
        <label for="inStock">In Stock: </label>
        <input v-model="toy.inStock" id="inStock" type="checkbox" />
        <button>Save</button>
        <button @click.prevent="removeToy(toy._id)">remove</button>
      </form>
    </div>
  </section>
</template>

<script>
import { toyService } from "../services/toy.service.js";
_ = require("lodash");

export default {
  name: "toy-edit",
  data() {
    return {
      toy: null
    };
  },
  computed: {
    isLoading() {
      return this.toy ? false : true;
    }
  },
  created() {
    this.loadToy();
  },

  watch: {
    "$route.params.id"() {
      this.loadToy();
    }
  },
  methods: {
    loadToy() {
      if (this.$route.params.id) {
        const id = this.$route.params.id;
        toyService.getById(id).then(toy => {
          this.toy = _.cloneDeep(toy);
        });
      } else {
        this.toy = {
          name: "",
          price: 0,
          type: "Funny",
          inStock: true
        };
      }
    },
    saveToy() {
      this.$store.dispatch({ type: "saveToy", toy: this.toy }).then(() => {
        this.$router.push("/toys");
      });
    },
    removeToy(id) {
      this.$store.dispatch({ type: "removeToy", id });
      this.$router.push(`/toys`);
    }
  }
};
</script>